.App {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.art_list_item {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1em;
    box-sizing: border-box;
    background-color: #fff;
    border: 1px solid #e7eaf1;
    border-radius: 3px;
    box-shadow: 0 1px 3px rgba(0,37,55,.06);
    margin-bottom: 15px;
}
.art_list_item_content {
    display: flex;
    flex-direction: column;
    cursor: pointer;
    font-size: 0.8em;
}
.art_list_item_content_title {
    font-size: 1.4em;
    font-weight: 600;
    color: #4e4e4e;
    margin-bottom: 10px;
}
.art_list_item_content_desc {
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.art_list_item_content_time {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
}
.art_list_item_tag {
    display: flex;
    align-items: center;
}
.art_list_item_tag_item {
    padding: 0px 8px;
    font-size: 0.6em;
    color: #a6abb2;
    border-radius: 2px;
    background-color: #fafafa;
    margin-right: 8px;
}
.art_list_item_date {
    display: flex;
    align-items: center;
}
.art_list_item_date img {
    width: 20px;
    height: 20px;
    margin-right: 8px;
}
.art_list_item_date {
    font-size: 0.8em;
}
.back_to_list {
    width: 100%;
    display: flex;
    align-items: center;
    font-size: 0.8em;
    position: absolute;
    left: 1em;
    top: 1em;
    cursor: pointer;
}
.back_to_list img {
    width: 15px;
    height: 15px;
    margin-right: 8px;
}